<template>
  <div class="hot">
    <h1 class="title-name">热门推荐</h1>
    <div class="tabs">
      <el-tabs v-model="activeName" class="demo-tabs" @tab-click="handleClick">
        <el-tab-pane
          :label="item.name"
          :name="index"
          v-for="(item, index) in hot"
          :key="item.id"
        >
          <div class="item" v-for="(item, index) in playListHot" :key="index">
            <div class="img-item">
              <el-image :src="item.coverImgUrl" />
              <span class="item-tab"
                ><i class="iconfont icon-erji"></i>{{ item.playCount }}/{{
                  item.trackCount
                }}首</span
              >
            </div>
            <div class="info">
              <el-tooltip
                class="box-item"
                effect="dark"
                :content="item.name"
                placement="bottom-start"
              >
                <p class="info-name">{{ item.name }}</p>
              </el-tooltip>

              <div class="tabs">
                <a class="tab" v-for="(sub, index) in item.tags" :key="index"
                  >#{{ sub }}</a
                >
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { ref } from '@vue/reactivity';
import { useStore } from 'vuex';
export default {
  name: "HotList",
  props: {
    hot: {
      type: Object,
      default() {
        return {};
      },
    },
    playListHot: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  setup() {
    const store = useStore()
    const activeName = ref('0');

    const handleClick = (tab) => {
        store.dispatch("getPlayListHot", tab.props.label);
    };

    return {
      handleClick,
      activeName,
    };
  },
};
</script>
<style lang="scss" scoped>
.hot {
  display: flex;
  align-items: flex-start;
 flex-direction: column;
  padding: 10px;
  border-radius: 10px;
  background: linear-gradient(145deg, #f6f6f6, #cfcfcf);
  box-shadow: 6px 6px 13px #bdbdbd, -6px -6px 13px #ffffff;
  .title-name {
    text-align: left;
    margin-right: 20px;
    width: 365px;
  }
  .item {
    position: relative;
    width: calc(100% / 6);
    margin: 20px;
    cursor: pointer;
    .item-tab {
      position: absolute;
      top: 0;
      right: 0;
      background: linear-gradient(145deg, #f6f6f607, #4b4b4b9a);
      width: 90%;
      text-align: right;
      font-size: var(--text14);
      color: #ffffff;
    }
  }
  .info {
    margin-top: 10px;
    font-size: var(--text14);
    text-align: left;
    .info-name {
      height: 35px;
      word-break: break-all;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2; /* 这里是超出几行省略 */
      overflow: hidden;
    }
    .tabs {
      margin-top: 10px;
      .tab {
        color: #ff641e;
        margin-right: 5px;
      }
    }
  }
}
.el-tab-pane {
  display: flex;
}
.el-image{
    border-radius: 5px ;
      transition: all 0.4s linear;
}
.item:hover {
  .el-image {
    transform: scale(1.1);
  }
}
</style>
