<template>
  <div id="rank">
    <div class="rank-top">
      <h1>{{ rankList.name }}</h1>
      <p>最近更新：{{ uptime }}</p>
    </div>
    <div
      class="rank-bottom"
      v-for="(item, index) in rankList.tracks"
      :key="index"
    >
      <div class="songimg">
        <el-image :src="item.al.picUrl"></el-image>
      </div>
      <div class="rankname">
        <p>{{ item.al.name }}</p>
        <div class="singger">
          <span v-for="(sub, index) in item.ar.slice(0, 1)" :key="index">{{
            sub.name
          }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";
import { ref } from "@vue/reactivity";
export default {
  name: "RankList",
  props: {
    rankList: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  setup(props) {
    const day = dayjs;

    const uptime = ref(day(props.rankList.updateTime).format("YYYY-MM-DD"));

    return {
      uptime,
    };
  },
};
</script>

<style lang="scss" scoped>
#rank {
  text-align: left;
  margin-top: 20px;
  max-width: calc(20% - 20px);
  max-height: 520px;
  overflow: hidden;
  padding: 20px;
  border-radius: 10px;
  background: linear-gradient(145deg, #f6f6f6, #cfcfcf);
  box-shadow: 6px 6px 13px #bdbdbd, -6px -6px 13px #ffffff;
  h1 {
    margin-bottom: 10px;
  }
  .rank-bottom {
    margin-top: 20px;
    display: flex;
    font-size: var(--text14);
    .songimg {
      width: 48px;
      height: 48px;
      margin-right: 20px;
    }
    .rankname {
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      cursor: pointer;
      .singger {
        display: flex;
      }
      p {
        display: block;
        margin-bottom: 5px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        transition: all .3s ease-in;
      }
    }
  }
}
.rank-bottom:hover {
  p {
    color: #ffa704;
    transform: translate(15px, 0);
    transition: all 0.3s ease-in;
  }
}
.el-image {
  border-radius: 5px;
}
</style>
