<template>
  <div class="mv">
    <h1 class="title-name">最新MV</h1>
    <div class="tabs">
      <el-tabs v-model="activeName" class="demo-tabs" @tab-click="handleClick">
        <el-tab-pane
          :label="item.name"
          :name="index"
          v-for="(item, index) in discInfoList"
          :key="item.id"
        >
          <div class="item" v-for="(sub, index) in newMv" :key="index">
            <div class="img-item">
              <el-image :src="sub.cover" />
            </div>
            <div class="info">
              <p class="info-name">{{ sub.name }}</p>
              <p class="info-name info-huise">{{ sub.artistName }}</p>
              <p class="info-name info-huise">
                <i class="iconfont icon-shipinbofang"></i>{{ sub.playCount }}
              </p>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { ref, reactive, toRefs } from "@vue/reactivity";
import { useStore } from "vuex";
export default {
  name: "MvList",
  props: {
    newMv: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  setup() {
    const store = useStore();
    const activeName = ref("0");
    const discInfo = reactive({
      discInfoList: [
        { name: "全部" },
        { name: "内地" },
        { name: "港台" },
        { name: "欧美" },
        { name: "日本" },
        { name: "韩国" },
      ],
    });
    const handleClick = (tab) => {
      store.dispatch("getNewMvList", tab.props.label);
    };

    return {
      handleClick,
      activeName,
      ...toRefs(discInfo),
    };
  },
};
</script>
<style lang="scss" scoped>
.mv {
  margin-top: 20px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding: 10px;
  border-radius: 10px;
  background: linear-gradient(145deg, #f6f6f6, #cfcfcf);
  box-shadow: 6px 6px 13px #bdbdbd, -6px -6px 13px #ffffff;
  .title-name {
    text-align: left;
    margin-right: 20px;
    width: 365px;
  }
  .item {
    position: relative;
    max-width: calc(100% / 6 - 40px);
    margin: 20px;
    cursor: pointer;
    .item-tab {
      position: absolute;
      top: 0;
      right: 0;
      background: linear-gradient(145deg, #f6f6f607, #b6b6b6);
      width: 90%;
      text-align: right;
      font-size: var(--text14);
    }
  }
  .info {
    margin-top: 10px;
    font-size: var(--text14);
    text-align: left;
    .info-name {
      height: 20px;
      word-break: break-all;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .info-huise {
      color: #999999;
    }
    .tabs {
      margin-top: 10px;
      .tab {
        color: #ff641e;
        margin-right: 5px;
      }
    }
  }
}
.el-tab-pane {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.el-image {
  border-radius: 5px;
  transition: all 0.4s linear;
}
.item:hover {
  .el-image {
    transform: scale(1.1);
  }
}
</style>
